.from-container {
    margin: 4rem 6rem;
    color: black;
}

.from-container form {
    padding-top: 3rem;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: auto;
}

.from-container p {
    padding-top: 1rem;
    font-size: 1.5rem;
    color: hsl(182, 58%, 55%);;
}

.from-container input {
    height: 3rem;
    padding: 0 0.5rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border-color: black;
}

.from-container textarea {
    padding: 1rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border-color: black;
}

.from-container select  {
    padding: 1rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border-color:black;

}

.from-container button{
    display: inline;
    height: 2rem;
    padding: 0 0.5rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border-color: black;
    font-weight: 680;
}

.from-container button:hover {
    background-color:  hsl(182, 58%, 55%);
    border-color:  hsl(182, 58%, 55%);
    color: white;
}

@media screen and (max-width: 850px) {
   .from-container {
    margin: 4rem 2rem;
   } 
   .from-container form {
    padding-top: 2rem;
    width: 90%;
}

}